import React, { useState } from 'react'
import { Card, Table, Button, Tooltip } from 'antd';
import Flex from 'components/shared-components/Flex'
import { Typography } from "antd";
import NumberFormat from 'react-number-format';
import { useHistory } from "react-router-dom";
import { PlusCircleOutlined, EyeOutlined } from '@ant-design/icons';
import GarageList from '../invoice';
import { Balance } from 'functions/balance';

const { Title } = Typography;

const Garage = ({systemData}) => {
  let history = useHistory();
  const newGarage = row => {
		history.push(`/app/apps/garage/add`)
	}
  	const { allTrucks, garageTools, garageToolsLoading, allCurrencyLoading, allCurrency, garages, garagesLoading} = systemData;
	const [toolsUsed, setToolsUsed] = useState(null)
	const [listVisible, setListVisible] = useState(false)
	const [garage, setGarage] = useState({});


	const showGarageList = garage => {
		setGarage(garage)
		let tools = garageToolsLoading ? [] : garageTools.docs.filter(x=> x.data().garageId == garage.data()._id);
		setToolsUsed(tools);
		setListVisible(true)
	};
	
	const closeGarageList = () => {
		setListVisible(false)
		setGarage({})
		setToolsUsed({});
	}
	
	const tableColumns = [
		{
			title: 'Date',
			dataIndex: 'id',
			render: (_,record) => Balance.getNormalDate(record.data()._id)
		},
		{
			title: 'Name',
			dataIndex: 'name',
			render: (_, record) => record.data().garageName
		},
		{
			title: 'Truck',
			dataIndex: 'id',
			render: (_,record) => `${allTrucks[record.data().truckId].model} ${allTrucks[record.data().truckId].plateNumber}`
		},
		{
			title: 'Description',
			dataIndex: 'description',
			render: (_, record) => record.data().description
		},
		{
			title: 'Price',
			dataIndex: 'amount',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().amount} 
							  suffix={` ${allCurrencyLoading ? '' : allCurrency[record.data().currency]?.name}`} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		
		{
			title: 'Rate',
			dataIndex: 'exchangeRate',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().exchangeRate} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Total RWF',
			dataIndex: 'totalAmount',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().totalAmount} 
							  suffix={' RWF'} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<Tooltip title="View">
						<Button type="primary" className="mr-2" icon={<EyeOutlined />} onClick={() => {showGarageList(elm)}} size="small"/>
					</Tooltip>
				</div>
			)
		}

	];

	return (
		<>
			{!listVisible ? <Card>
				<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
						<Flex className="mb-1" mobileFlex={false}>
							<div className="mr-md-3 mb-3">
								<Title level={4}>Garage activities historic</Title>
							</div>
						</Flex>
						<div>
							<Button size="small" onClick={newGarage} type="primary" icon={<PlusCircleOutlined />} block>New Garage</Button>
						</div>
					</Flex>
				<div className="table-responsive">
					<Table 
						columns={tableColumns} 
						dataSource={!garagesLoading ? garages.docs.reverse() : []} 
						loading={garagesLoading}
						rowKey='_id' 
					/>
				</div>
			</Card>
			:
			<GarageList 
				toolsUsed={toolsUsed} 
				garage={garage} 
				allTrucks={allTrucks}
				allCurrency={allCurrency}
				allCurrencyLoading={allCurrencyLoading}
				closeGarageList={closeGarageList} 
			/>}
		</>
	)
}

export default Garage
